import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { componentsList, classesList } from '../../utilities/widgets';
import { menuListInterface } from '../../utilities/interface';

@IonicPage()
@Component({
  selector: 'page-menu',
  templateUrl: 'menu.html',
})
export class MenuPage {

  rootPage: any = 'DesignerPage';
  componentsList: Array<menuListInterface> = componentsList;
  classesList: Array<menuListInterface> = classesList;
  status: boolean = true;
  mode: string = 'Editing';

  constructor(public navCtrl: NavController, public navParams: NavParams, public events: Events) { }

  changeMode() {
    this.status = !this.status;
    this.status == true ? this.mode = 'Editing' : this.mode = 'Active';
    this.events.publish('page-menu : page-designer : mode', this.mode);
  }

  addComponent(component: menuListInterface) {
    component.component ?
      this.events.publish('page-menu : page-designer : add-component', component) :
      this.events.publish('page-menu : global-service : add-class', component);
  }

  statusBarEvent(value) {
    switch (value) {
      case 'changeMode':
        this.changeMode();
        break;
      case 'connect':
        this.events.publish('page-menu : socket-service : connect-to-board');
        break;
      case 'initialize':
        this.events.publish('page-menu : socket-service : initialize');
        break;
      default:
        break;
    }
  }
}



